import React, { useState } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption
} from 'reactstrap';

import { CarouselImage } from '../Styles/layout';

import image1 from "../images/anton-darius-ebHCU8n7G38-unsplash.jpg"
import image2 from "../images/kenrick-mills-1h2Pg97SXfA-unsplash.jpg"
import image3 from "../images/sam-ueGaQiHV86o-unsplash.jpg"

const items = [
  {
    
    src: image1,
    altText: 'Slide 1',
    
  },
  {
    src: image2,
    altText: 'Slide 2',
    
  },
  {
    src: image3,
    altText: 'Slide 3',
    
  }
];

const CarouselPage = (props) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  }

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  }

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  }

  const slides = items.map((item) => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={item.src}
      >
      <CarouselImage>
        <img src={item.src} 
        width={900} 
        height={500} 
        margin={"auto"}
        
        
        alt={item.altText} />
      </CarouselImage>
        <CarouselCaption captionText={item.caption} captionHeader={item.caption} />
      
      </CarouselItem>
    );
  });

  return (
    <Carousel
      activeIndex={activeIndex}
      next={next}
      previous={previous}
    >
      <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={goToIndex} />
      {slides}
      <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
      <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
    </Carousel>
  );
}




export default CarouselPage;